import React, { useState, useEffect } from "react";
import { Service } from "../../entities/Service"; // Relativ: Zwei Ebenen hoch zu src/entities/Service
import { Link } from "react-router-dom";
import { createPageUrl } from "../../utils"; // Relativ: Zwei Ebenen hoch zu src/utils
import { Card, CardContent, CardHeader, CardTitle } from "../ui/Card"; // Relativ
import { Button } from "../ui/Button"; // Relativ
import { Badge } from "../ui/Badge"; // Relativ
import { Heart, Clock, Users, ArrowRight } from "lucide-react";
import { motion } from "framer-motion";

export default function ServicesPreview() {
  const [services, setServices] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadFeaturedServices();
  }, []);

  const loadFeaturedServices = () => {
    // Synchron, da Service ein Array ist
    const allServices = Service;
    const featured = allServices.filter(s => s.featured).slice(0, 3);
    setServices(featured);
    setLoading(false);
  };

  const typeLabels = {
    einzelcoaching: "Einzelcoaching",
    gruppenkurs: "Gruppenkurs",
    online_kurs: "Online Kurs",
    workshop: "Workshop"
  };

  const typeIcons = {
    einzelcoaching: Heart,
    gruppenkurs: Users,
    online_kurs: Clock,
    workshop: Users
  };

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-12">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {[1, 2, 3].map((i) => (
            <Card key={i} className="animate-pulse">
              <CardContent className="h-64 bg-gray-200" />
            </Card>
          ))}
        </div>
      </div>
    );
  }

  if (services.length === 0) {
    return null;
  }

  return (
    <section className="py-12">
      <div className="container mx-auto px-4">
        <motion.h2 
          initial={{ opacity: 0, y: 20 }} 
          animate={{ opacity: 1, y: 0 }} 
          className="text-3xl font-bold text-center mb-8"
        >
          Transformative Begleitung
        </motion.h2>
        <p className="text-center mb-12">
          Individuelle Coaching-Programme und Kurse für Ihre persönliche Entwicklung und spirituelles Wachstum.
        </p>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {services.map((service, index) => {
            const TypeIcon = typeIcons[service.type] || Heart;
            return (
              <motion.div 
                key={index} 
                initial={{ opacity: 0, y: 20 }} 
                animate={{ opacity: 1, y: 0 }} 
                transition={{ delay: index * 0.2 }}
              >
                <Card>
                  <CardHeader>
                    <Badge><TypeIcon className="inline mr-2" /> {typeLabels[service.type]}</Badge>
                    <CardTitle>{service.title}</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <img src={service.image_url} alt={service.title} className="w-full h-48 object-cover mb-4" />
                    <p className="mb-2">€{service.price}</p>
                    {service.duration && <p className="mb-4">{service.duration}</p>}
                    <p>{service.description}</p>
                    {service.benefits && service.benefits.length > 0 && (
                      <div className="mt-4">
                        <h4 className="font-semibold mb-2">Ihre Vorteile:</h4>
                        <ul className="list-disc pl-6">
                          {service.benefits.slice(0, 3).map((benefit, i) => (
                            <li key={i}>{benefit}</li>
                          ))}
                        </ul>
                      </div>
                    )}
                    <Button className="mt-4">Jetzt buchen <ArrowRight className="ml-2" /></Button>
                  </CardContent>
                </Card>
              </motion.div>
            );
          })}
        </div>
        <div className="text-center mt-8">
          <Link to={createPageUrl("Services")}>
            <Button>Alle Coaching-Angebote ansehen</Button>
          </Link>
        </div>
      </div>
    </section>
  );
}
