import React, { useState, useEffect } from "react";
import { Service } from "../entities/Service"; // Relativ: Eine Ebene hoch zu src/entities/Service
import { Card, CardContent, CardHeader, CardTitle } from "../components/ui/Card";
import { Button } from "../components/ui/Button";
import { Badge } from "../components/ui/Badge";
import Tabs from "../components/ui/Tabs"; // Default-Import für Tabs
import { TabsList, TabsTrigger } from "../components/ui/Tabs"; // Named Exports
import { Heart, Clock, Users, Star, ArrowRight, CheckCircle } from "lucide-react";
import { motion } from "framer-motion";


export default function Services() {
  const [services, setServices] = useState([]);
  const [filteredServices, setFilteredServices] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeType, setActiveType] = useState("all");

  useEffect(() => {
    loadServices();
  }, []);

  useEffect(() => {
    filterServices();
  }, [services, activeType]);

  const loadServices = () => {
    // Synchron, da Service ein Array ist
    setServices(Service.sort((a, b) => new Date(b.created_date) - new Date(a.created_date))); // Simuliere '-created_date'
    setLoading(false);
  };

  const filterServices = () => {
    let filtered = services;
    if (activeType !== "all") {
      filtered = filtered.filter(s => s.type === activeType);
    }
    setFilteredServices(filtered);
  };

  const typeLabels = {
    all: "Alle Angebote",
    einzelcoaching: "Einzelcoaching",
    gruppenkurs: "Gruppenkurse",
    online_kurs: "Online Kurse",
    workshop: "Workshops"
  };

  const typeIcons = {
    einzelcoaching: Heart,
    gruppenkurs: Users,
    online_kurs: Clock,
    workshop: Star
  };

  const types = ["einzelcoaching", "gruppenkurs", "online_kurs", "workshop"];

  if (loading) {
    return <div>Lade Services...</div>;
  }

  return (
    <div className="container mx-auto px-4 py-12">
      <motion.h1 
        initial={{ opacity: 0, y: 20 }} 
        animate={{ opacity: 1, y: 0 }} 
        className="text-4xl font-bold text-center mb-8"
      >
        Services
      </motion.h1>
      <p className="text-lg text-center mb-12">
        Transformative Begleitung für Ihre persönliche Entwicklung. Finden Sie den Weg zu Ihrem authentischen Selbst und innerer Balance.
      </p>
      <Tabs defaultValue="all" onValueChange={setActiveType}>
        <TabsList className="justify-center mb-8">
          {['all', ...types].map(type => (
            <TabsTrigger key={type} value={type}>{typeLabels[type]}</TabsTrigger>
          ))}
        </TabsList>
      </Tabs>
      <p className="text-center mb-6">{filteredServices.length} Angebote verfügbar</p>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredServices.map((service, index) => (
          <Card key={index}>
            <CardHeader>
              <CardTitle>{service.title}</CardTitle>
              <Badge>€{service.price}</Badge>
              {service.duration && <Badge>{service.duration}</Badge>}
            </CardHeader>
            <CardContent>
              <p>{service.description}</p>
              {service.benefits && service.benefits.length > 0 && (
                <ul className="list-disc pl-6 mt-4">
                  {service.benefits.map((benefit, i) => (
                    <li key={i}><CheckCircle className="inline mr-2" />{benefit}</li>
                  ))}
                </ul>
              )}
              {service.available_slots && <Badge>Noch {service.available_slots} Plätze verfügbar</Badge>}
              <Button className="mt-4">Buchen <ArrowRight className="ml-2" /></Button>
            </CardContent>
          </Card>
        ))}
      </div>
      {filteredServices.length === 0 && (
        <p className="text-center mt-8">Keine Angebote in dieser Kategorie verfügbar. Wählen Sie eine andere Kategorie oder schauen Sie später wieder vorbei.</p>
      )}
      <div className="text-center mt-12">
        <Button>Beginnen Sie Ihre Reise zu innerem Wachstum und spiritueller Klarheit. Vereinbaren Sie noch heute ein kostenloses Erstgespräch.</Button>
      </div>
    </div>
  );
}
