import React, { useState, useEffect } from "react";
import { Product } from "../entities/Product"; // Relativ: Eine Ebene hoch zu src/entities/Product
import { Card, CardContent, CardHeader, CardTitle } from "../components/ui/Card";
import { Badge } from "../components/ui/Badge";
import { Button } from "../components/ui/Button";
import { Input } from "../components/ui/Input";
import Tabs from "../components/ui/Tabs"; // Default-Import für Tabs
import { TabsList, TabsTrigger } from "../components/ui/Tabs"; // Named Exports
import { Sparkles, ShoppingBag, Search, Filter } from "lucide-react";
import { motion } from "framer-motion";

export default function Shop() {
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [activeCategory, setActiveCategory] = useState("all");

  useEffect(() => {
    loadProducts();
  }, []);

  useEffect(() => {
    filterProducts();
  }, [products, searchTerm, activeCategory]);

  const loadProducts = () => {
    // Synchron, da Product ein Array ist
    setProducts(Product);
    setLoading(false);
  };

  const filterProducts = () => {
    let filtered = products;
    if (activeCategory !== "all") {
      filtered = filtered.filter(p => p.category === activeCategory);
    }
    if (searchTerm) {
      filtered = filtered.filter(p => 
        p.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        p.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
        (p.healing_properties && p.healing_properties.some(prop => prop.toLowerCase().includes(searchTerm.toLowerCase())))
      );
    }
    setFilteredProducts(filtered);
  };

  const categoryLabels = {
    all: "Alle Produkte",
    heilsteine: "Heilsteine",
    heilketten: "Heilketten",
    kerzen: "Mystische Kerzen"
  };

  if (loading) {
    return <div>Lade Produkte...</div>;
  }

  return (
    <div className="container mx-auto px-4 py-12">
      <motion.h1 
        initial={{ opacity: 0, y: 20 }} 
        animate={{ opacity: 1, y: 0 }} 
        className="text-4xl font-bold text-center mb-8"
      >
        Shop
      </motion.h1>
      <p className="text-lg text-center mb-12">
        Entdecken Sie unsere sorgfältig ausgewählten Heilsteine, kraftvollen Schmuckstücke und mystischen Kerzen für Ihre spirituelle Reise.
      </p>
      <div className="flex justify-between mb-8">
        <Input 
          placeholder="Suche nach Produkten..." 
          value={searchTerm} 
          onChange={(e) => setSearchTerm(e.target.value)} 
          className="max-w-md"
        />
        <Tabs defaultValue="all" onValueChange={setActiveCategory}>
          <TabsList>
            {Object.keys(categoryLabels).map(cat => (
              <TabsTrigger key={cat} value={cat}>{categoryLabels[cat]}</TabsTrigger>
            ))}
          </TabsList>
        </Tabs>
      </div>
      <p className="text-center mb-6">{filteredProducts.length} Produkte gefunden</p>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredProducts.map((product, index) => (
          <Card key={index}>
            <CardHeader>
              <CardTitle>{product.name}</CardTitle>
              <Badge>€{product.price}</Badge>
            </CardHeader>
            <CardContent>
              <img src={product.image_url} alt={product.name} className="w-full h-48 object-cover mb-4" />
              <p>{product.description}</p>
              {product.healing_properties && product.healing_properties.length > 0 && (
                <ul className="list-disc pl-6 mt-4">
                  {product.healing_properties.map((prop, i) => (
                    <li key={i}><Sparkles className="inline mr-2" />{prop}</li>
                  ))}
                </ul>
              )}
              <Button className="mt-4"><ShoppingBag className="mr-2" /> In den Warenkorb</Button>
            </CardContent>
          </Card>
        ))}
      </div>
      {filteredProducts.length === 0 && (
        <p className="text-center mt-8">Keine Produkte gefunden. Versuchen Sie andere Suchbegriffe oder wählen Sie eine andere Kategorie.</p>
      )}
    </div>
  );
}
