import React, { useState, useEffect } from "react";
import { Product } from "../../entities/Product"; // Relativ: Zwei Ebenen hoch zu src/entities/Product
import { Card, CardContent, CardHeader, CardTitle } from "../ui/Card"; // Relativ: Eine Ebene hoch zu src/components/ui/Card
import { Badge } from "../ui/Badge"; // Relativ
import { Button } from "../ui/Button"; // Relativ
import { Sparkles, ShoppingBag } from "lucide-react";
import { motion } from "framer-motion";

export default function FeaturedProducts() {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadFeaturedProducts();
  }, []);

  const loadFeaturedProducts = () => {
    // Synchron, da Product ein Array ist
    const allProducts = Product;
    const featured = allProducts.filter(p => p.featured).slice(0, 3);
    setProducts(featured);
    setLoading(false);
  };

  const categoryLabels = {
    heilsteine: "Heilsteine",
    heilketten: "Heilketten",
    kerzen: "Mystische Kerzen"
  };

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-12">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {[1, 2, 3].map((i) => (
            <Card key={i} className="animate-pulse">
              <CardContent className="h-64 bg-gray-200" />
            </Card>
          ))}
        </div>
      </div>
    );
  }

  if (products.length === 0) {
    return null;
  }

  return (
    <section className="py-12 bg-gray-50">
      <div className="container mx-auto px-4">
        <motion.h2 
          initial={{ opacity: 0, y: 20 }} 
          animate={{ opacity: 1, y: 0 }} 
          className="text-3xl font-bold text-center mb-8"
        >
          Spirituelle Schätze
        </motion.h2>
        <p className="text-center mb-12">
          Handverlesene Heilsteine, kraftvolle Schmuckstücke und mystische Kerzen für Ihre spirituelle Reise.
        </p>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {products.map((product, index) => (
            <motion.div 
              key={index} 
              initial={{ opacity: 0, y: 20 }} 
              animate={{ opacity: 1, y: 0 }} 
              transition={{ delay: index * 0.2 }}
            >
              <Card>
                <CardHeader>
                  <Badge>{categoryLabels[product.category]}</Badge>
                  <CardTitle>{product.name}</CardTitle>
                </CardHeader>
                <CardContent>
                  <img src={product.image_url} alt={product.name} className="w-full h-48 object-cover mb-4" />
                  <p>{product.description}</p>
                  {product.healing_properties && product.healing_properties.length > 0 && (
                    <div className="mt-4">
                      <h4 className="font-semibold mb-2">Heilende Wirkung:</h4>
                      <ul className="list-disc pl-6">
                        {product.healing_properties.slice(0, 3).map((property, i) => (
                          <li key={i}>{property}</li>
                        ))}
                      </ul>
                    </div>
                  )}
                  <div className="mt-4 flex justify-between items-center">
                    <span className="font-bold">€{product.price}</span>
                    <Button><ShoppingBag className="mr-2" /> In den Warenkorb</Button>
                  </div>
                </CardContent>
              </Card>
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  );
}
