import React from "react";
import { Link, useLocation } from "react-router-dom";
import { createPageUrl } from "../utils";
import { Home, ShoppingBag, Heart, User, Mail, Sparkles } from "lucide-react";

export default function Layout({ children }) { // Entfernt currentPageName, da nicht verwendet
  const location = useLocation();
  const navigation = [
    { name: "Startseite", href: createPageUrl("Home"), icon: Home },
    { name: "Shop", href: createPageUrl("Shop"), icon: ShoppingBag },
    { name: "Coaching", href: createPageUrl("Services"), icon: Heart },
    { name: "Über mich", href: createPageUrl("About"), icon: User },
    { name: "Kontakt", href: createPageUrl("Contact"), icon: Mail },
  ];

  return (
    <div className="min-h-screen flex flex-col">
      <header className="bg-white shadow">
        <nav className="container mx-auto px-4 py-4 flex justify-between items-center">
          <Link to={createPageUrl("Home")} className="text-2xl font-bold flex items-center">
            <Sparkles className="mr-2" /> Seelenwege
          </Link>
          <ul className="flex gap-6">
            {navigation.map((item) => (
              <li key={item.name}>
                <Link 
                  to={item.href} 
                  className={`flex items-center ${location.pathname === item.href ? 'text-blue-600' : 'text-gray-600'}`}
                >
                  <item.icon className="mr-2 w-5 h-5" />
                  {item.name}
                </Link>
              </li>
            ))}
          </ul>
        </nav>
      </header>
      <main className="flex-grow">
        {children}
      </main>
      <footer className="bg-gray-800 text-white py-6">
        <div className="container mx-auto px-4 text-center">
          © 2025 Seelenwege. Alle Rechte vorbehalten.
        </div>
      </footer>
    </div>
  );
}
