import React from "react";
import { Link } from "react-router-dom";
import { createPageUrl } from "../../utils"; // Relativ, wie zuvor
import { Button } from "../ui/Button"; // Relativ
import { Sparkles, Heart, Star, Users, Award } from "lucide-react"; // Hinzugefügt: Users und Award (falls Award auch fehlte)
import { motion } from "framer-motion";

export default function HeroSection() {
  return (
    <section className="relative bg-gradient-to-r from-purple-600 to-indigo-600 text-white py-24">
      {/* Animated background elements */}
      <div className="absolute inset-0 overflow-hidden">
        <motion.div 
          animate={{ x: [0, 100, 0], y: [0, 50, 0] }} 
          transition={{ duration: 10, repeat: Infinity }}
          className="absolute top-0 left-0 w-64 h-64 bg-white opacity-10 rounded-full"
        />
        <motion.div 
          animate={{ x: [0, -100, 0], y: [0, -50, 0] }} 
          transition={{ duration: 15, repeat: Infinity }}
          className="absolute bottom-0 right-0 w-96 h-96 bg-white opacity-5 rounded-full"
        />
      </div>
      <div className="container mx-auto px-4 text-center relative z-10">
        {/* Mystical icon */}
        <Sparkles className="w-16 h-16 mx-auto mb-6" />
        {/* Hauptüberschrift */}
        <motion.h1 
          initial={{ opacity: 0, y: 20 }} 
          animate={{ opacity: 1, y: 0 }} 
          className="text-5xl font-bold mb-4"
        >
          Entdecke Deine Seelenwege
        </motion.h1>
        <p className="text-xl mb-8">
          Authentische Lebensberatung und spirituelles Coaching für Deine persönliche Transformation.
          Finde Klarheit, Balance und Dein wahres Selbst.
        </p>
        {/* Trust indicators */}
        <div className="flex justify-center gap-8 mb-8">
          <div><Star className="inline mr-2" /> 10+ Jahre Erfahrung</div>
          <div><Users className="inline mr-2" /> 500+ zufriedene Klienten</div>
          <div><Award className="inline mr-2" /> Zertifizierte Beraterin</div>
        </div>
        {/* CTA Buttons */}
        <div className="flex justify-center gap-4">
          <Link to={createPageUrl("Contact")}>
            <Button variant="secondary">Kostenloses Erstgespräch</Button>
          </Link>
          <Link to={createPageUrl("Shop")}>
            <Button>Heilsteine entdecken</Button>
          </Link>
        </div>
      </div>
    </section>
  );
}
